import WebSocket from 'ws';
import EventEmitter from 'eventemitter3';
import { v4 as uuidv4 } from 'uuid';
export class SyncManager {
    constructor(options) {
        this.ws = null;
        this.reconnectAttempts = 0;
        this.entitySubscriptions = new Set();
        this.options = {
            reconnectInterval: 5000,
            maxReconnectAttempts: 5,
            ...options
        };
        this.eventEmitter = new EventEmitter();
    }
    connect() {
        if (this.ws?.readyState === WebSocket.OPEN)
            return;
        this.ws = new WebSocket(this.options.url);
        this.ws.on('open', () => {
            console.log('WebSocket connection established');
            this.reconnectAttempts = 0;
            this.resubscribeEntities();
        });
        this.ws.on('message', (data) => {
            try {
                const update = JSON.parse(data);
                this.eventEmitter.emit(`${update.entity}:update`, update);
            }
            catch (error) {
                console.error('Error processing WebSocket message:', error);
            }
        });
        this.ws.on('close', () => {
            console.log('WebSocket connection closed');
            this.handleReconnect();
        });
        this.ws.on('error', (error) => {
            console.error('WebSocket error:', error);
            this.handleReconnect();
        });
    }
    handleReconnect() {
        if (this.reconnectAttempts >= this.options.maxReconnectAttempts) {
            console.error('Max reconnection attempts reached');
            return;
        }
        setTimeout(() => {
            this.reconnectAttempts++;
            this.connect();
        }, this.options.reconnectInterval);
    }
    resubscribeEntities() {
        if (!this.ws || this.ws.readyState !== WebSocket.OPEN)
            return;
        this.entitySubscriptions.forEach(entity => {
            this.ws.send(JSON.stringify({
                type: 'SUBSCRIBE',
                entity,
                id: uuidv4()
            }));
        });
    }
    subscribe(entity, callback) {
        this.entitySubscriptions.add(entity);
        if (this.ws?.readyState === WebSocket.OPEN) {
            this.ws.send(JSON.stringify({
                type: 'SUBSCRIBE',
                entity,
                id: uuidv4()
            }));
        }
        this.eventEmitter.on(`${entity}:update`, callback);
        return () => {
            this.entitySubscriptions.delete(entity);
            this.eventEmitter.off(`${entity}:update`, callback);
            if (this.ws?.readyState === WebSocket.OPEN) {
                this.ws.send(JSON.stringify({
                    type: 'UNSUBSCRIBE',
                    entity,
                    id: uuidv4()
                }));
            }
        };
    }
    async sendUpdate(entity, update) {
        if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
            throw new Error('WebSocket connection not established');
        }
        const fullUpdate = {
            ...update,
            timestamp: Date.now()
        };
        this.ws.send(JSON.stringify(fullUpdate));
    }
    disconnect() {
        if (this.ws) {
            this.ws.close();
            this.ws = null;
        }
        this.eventEmitter.removeAllListeners();
        this.entitySubscriptions.clear();
    }
}
