import { BaseService } from './baseService';
import { FolderContext } from '../folderContext';
import { ENV, HEADERS } from '../utils/constants';
/**
 * Service for managing UiPath Actions.
 *
 * Actions are task-based automation components that can be integrated into
 * applications and processes. They represent discrete units of work that can
 * be triggered and monitored through the UiPath API.
 *
 * @see {@link https://docs.uipath.com/automation-cloud/docs/actions|Actions Documentation}
 */
export class ActionsService extends BaseService {
    constructor(config, executionContext) {
        super(config, executionContext);
        this.folderContext = new FolderContext(config, executionContext);
    }
    /**
     * Creates a new action
     */
    async create(action) {
        const response = await this.post('/actions', action);
        return response.data;
    }
    /**
     * Updates an existing action
     */
    async update(id, action) {
        const response = await this.put(`/actions/${id}`, action);
        return response.data;
    }
    /**
     * Retrieves an action by ID
     */
    async retrieve(id) {
        const response = await this.get(`/actions/${id}`);
        return response.data;
    }
    /**
     * Deletes an action by ID
     */
    async deleteAction(id) {
        await this.delete(`/actions/${id}`);
    }
    /**
     * Lists all actions
     */
    async list() {
        const response = await this.get('/actions');
        return response.data;
    }
    /**
     * Creates a new action from a schema
     */
    async createFromSchema(appName, appFolderPath, actionName, actionSchema) {
        const spec = this.getCreateFromSchemaSpec(appName, appFolderPath, actionName, actionSchema);
        const response = await this.requestWithSpec(spec);
        return response.data;
    }
    /**
     * Retrieves the schema for an action app
     */
    async retrieveAppSchema(appName, appFolderPath) {
        const spec = this.retrieveAppKeySpec(appName);
        const response = await this.request(spec.method, spec.url, {
            headers: spec.headers,
            params: spec.params
        });
        const deployedApp = this.extractDeployedApp(response.data.deployed, appFolderPath);
        return deployedApp.actionSchema;
    }
    getCreateFromSchemaSpec(appName, appFolderPath, actionName, actionSchema) {
        const tenantId = process.env[ENV.TENANT_ID];
        if (!tenantId) {
            throw new Error(`${ENV.TENANT_ID} env var is not set`);
        }
        return {
            method: 'POST',
            url: '/apps_/default/api/v1/default/deployed-action-apps-schemas/actions',
            data: {
                name: actionName,
                schema: actionSchema
            },
            headers: { [HEADERS.TENANT_ID]: tenantId }
        };
    }
    retrieveAppKeySpec(appName) {
        const tenantId = process.env[ENV.TENANT_ID];
        if (!tenantId) {
            throw new Error(`${ENV.TENANT_ID} env var is not set`);
        }
        return {
            method: 'GET',
            url: '/apps_/default/api/v1/default/deployed-action-apps-schemas',
            params: { search: appName },
            headers: { [HEADERS.TENANT_ID]: tenantId }
        };
    }
    extractDeployedApp(deployed, appFolderPath) {
        const deployedApp = deployed.find(app => app.deploymentFolder.fullyQualifiedName === appFolderPath);
        if (!deployedApp) {
            throw new Error(`No deployed app found in folder ${appFolderPath}`);
        }
        return deployedApp;
    }
}
