export class EntitySyncHandler {
    constructor(syncManager, entityName) {
        this.syncManager = syncManager;
        this.entityName = entityName;
        this.state = {
            items: [],
            lastUpdate: 0
        };
        this.unsubscribe = null;
    }
    subscribe(onUpdate) {
        // Unsubscribe from any existing subscription
        if (this.unsubscribe) {
            this.unsubscribe();
        }
        this.unsubscribe = this.syncManager.subscribe(this.entityName, (update) => {
            this.handleUpdate(update);
            onUpdate(this.state);
        });
        return () => {
            if (this.unsubscribe) {
                this.unsubscribe();
                this.unsubscribe = null;
            }
        };
    }
    handleUpdate(update) {
        const { type, data, timestamp } = update;
        switch (type) {
            case 'CREATE':
                this.state.items.push(data);
                break;
            case 'UPDATE': {
                const index = this.state.items.findIndex(item => this.getEntityId(item) === this.getEntityId(data));
                if (index !== -1) {
                    this.state.items[index] = data;
                }
                break;
            }
            case 'DELETE': {
                this.state.items = this.state.items.filter(item => this.getEntityId(item) !== this.getEntityId(data));
                break;
            }
        }
        this.state.lastUpdate = timestamp;
    }
    async create(data) {
        await this.syncManager.sendUpdate(this.entityName, {
            type: 'CREATE',
            entity: this.entityName,
            data
        });
    }
    async update(data) {
        await this.syncManager.sendUpdate(this.entityName, {
            type: 'UPDATE',
            entity: this.entityName,
            data
        });
    }
    async delete(data) {
        await this.syncManager.sendUpdate(this.entityName, {
            type: 'DELETE',
            entity: this.entityName,
            data
        });
    }
    getState() {
        return this.state;
    }
}
